body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    width: 100vw;
    height: 100vh;
    background:-webkit-linear-gradient(left, rgb(255, 207, 183), rgb(247, 207, 200));
    background:linear-gradient(to right, rgb(255, 207, 183), rgb(247, 207, 200));
    margin-top: 0;
}



.counter{
    height: 15vh;
}

.tick{
    height: 30px;
}
